
.detail-item {
  font-size: 14px;
  box-sizing: border-box;
  padding: 0 20px;
}
.detail-item .di-header {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: horizontal;
    -webkit-box-direction: normal;
        -ms-flex-direction: row;
            flex-direction: row;
    -ms-flex-line-pack: center;
        align-content: center;
    -webkit-box-pack: justify;
        -ms-flex-pack: justify;
            justify-content: space-between;
}
.detail-item .di-header .di-header-value {
      color: #333;
}
.detail-item .di-header small {
      padding-left: 5px;
      font-size: 10px;
      color: #888;
}
.detail-item .di-chart {
    margin-top: 10px;
}



