
.landing {
  width: 600px;
}
.landing .options {
    padding-bottom: 30px;
}
.landing .options .options-title {
      color: #555;
      margin: 0 0 5px 0;
      cursor: pointer;
      display: inline-block;
}
.landing .options .options-field label {
      font-size: 12px;
      color: #888;
      display: inline-block;
      width: 80px;
}
.landing .options .options-field input {
      width: 300px;
}
.landing .options button {
      margin: 10px 2px;
}
.landing .options .api-status {
      font-size: 12px;
      padding: 5px 5px;
      border: solid 1px #eee;
      color: #888;
      margin-top: 10px;
}
.landing .connection-table {
    font-size: 12px;
    border-collapse: collapse;
    color: #888;
    width: 100%;
}
.landing .connection-table th {
      font-weight: normal;
      padding: 8px 5px 8px 5px;
}
.landing .connection-table > tr {
      border-bottom: solid 1px #e2e2e2;
}



